/* source-sans-pro-400normal - latin */
@font-face {
  font-family: 'Source Sans Pro';
  font-style: normal;
  font-display: swap;
  font-weight: 400;
  src:
    local('Source Sans Pro Regular '),
    local('Source Sans Pro-Regular'),
    url('../fonts/source-sans-pro-latin-400.woff2') format('woff2'), /* Super Modern Browsers */
    url('../fonts/source-sans-pro-latin-400.woff') format('woff'); /* Modern Browsers */
}

/* source-sans-pro-400italic - latin */
@font-face {
  font-family: 'Source Sans Pro';
  font-style: italic;
  font-display: swap;
  font-weight: 400;
  src:
    local('Source Sans Pro Regular italic'),
    local('Source Sans Pro-Regularitalic'),
    url('../fonts/source-sans-pro-latin-400italic.woff2') format('woff2'), /* Super Modern Browsers */
    url('../fonts/source-sans-pro-latin-400italic.woff') format('woff'); /* Modern Browsers */
}

